import { useStorage } from "@vueuse/core"
import type { PublisherAccount } from "~/types/api"
import type { Role } from "~/types/app"

export const useUserStore = defineStore("user", () => {
  const token = useStorage<string>("st", null, localStorage)
  const data = ref<null | PublisherAccount>(null)
  const isAuthorized = computed(() => Boolean(data.value))
  const role = computed<Role>(() => (isAuthorized.value ? "user" : "guest"))

  async function getData() {
    if (!token.value) {
      data.value = null
      return
    }

    try {
      data.value = await getAccountData()
      return data.value
    } catch (e) {
      if (e instanceof APIError && e.hasCode("ERROR_INVALID_STAFF_TOKEN")) {
        logout()
      }

      throw e
    }
  }

  async function login(newToken: null | string) {
    token.value = newToken
    return await getData()
  }

  function logout() {
    data.value = null
    token.value = null
  }

  return {
    token: readonly(token),
    isAuthorized,
    role,
    data: readonly(data),
    getData,
    login,
    logout,
  }
})

if (import.meta.hot) {
  import.meta.hot.accept(acceptHMRUpdate(useUserStore, import.meta.hot))
}
