import { useNotification as useN } from "@kyvg/vue3-notification"

export function useNotification() {
  const route = useRoute()
  const { notify } = useN()

  async function notifySuccess(text: string, id?: number | string) {
    notify.close(id)
    await new Promise((resolve) => setTimeout(resolve, 300)) // wait for close animation
    notify.close(id) // close immediately if it was open while waiting

    return notify({
      id: id as number,
      type: "success",
      text: text,
      duration: 3000,
    })
  }

  async function notifyError(text: string, id?: number | string) {
    watch(
      () => route.path,
      () => {
        notify.close(id)
      }
    )

    notify.close(id)
    await new Promise((resolve) => setTimeout(resolve, 300)) // wait for close animation
    notify.close(id) // close immediately if it was open while waiting

    return notify({
      id: id as number,
      type: "error",
      text: text,
      duration: -1,
    })
  }

  return {
    notifySuccess,
    notifyError,
  }
}
