import axios from "axios"
import type { AxiosRequestConfig, Method } from "axios"
import type { Response, ResponseErrorCode } from "~/types/api"

export class APIError extends Error {
  readonly response: Response

  constructor(response: Response) {
    const message = response.errors.map((error) => error.code).join(", ")
    super(message)
    this.response = response
  }

  hasCode(code: ResponseErrorCode) {
    return this.response.errors.some((error) => error.code === code)
  }
}

interface Options extends AxiosRequestConfig {
  silent?: boolean
}

function prepareUrl(url: string) {
  if (url.endsWith("/")) {
    return url
  }

  return url + "/"
}

async function request<T>(method: Method, url: string, options: Options = {}) {
  const config = useRuntimeConfig()
  const { token } = storeToRefs(useUserStore())

  const client = axios.create({
    baseURL: config.public.apiBaseUrl,
    params: {
      st: token.value,
    },
  })

  const response = await client.request<Response<T>>({
    method,
    url: prepareUrl(url),
    ...options,
  })

  if (!options.silent && !response.data.success) {
    const { notifyError } = useNotification()
    for (const error of response.data.errors) {
      notifyError(error.translated, url)
    }
  }

  if (!response.data.success) {
    throw new APIError(response.data)
  }

  return response.data.data
}

export function get<T>(url: string, options: Options = {}) {
  return request<T>("GET", url, options)
}

export function post<T>(url: string, options: Options = {}) {
  return request<T>("POST", url, {
    ...options,
    headers: {
      "Content-Type": "multipart/form-data",
    },
  })
}
